	module.exports = {"data":{"markdownRemark":{"html":"<blockquote>\n<p>Message 1555 : Mon Mar  3 (Vakaur)     :: Trap and ambush changes</p>\n</blockquote>\n<ul>\n<li>Trap is now operational.  You should no longer get the dreaded\n'You can not do it this way' message when trying to lay a trap.</li>\n<li>Trap applies a bash affection when it lands.</li>\n<li>Traps will no longer be instantly disarmed if a player sees the\nperson laying the trap.  The trap will just be less likely to\nsucceed.</li>\n<li>Trap damage is now based 100% on ambush damage.</li>\n<li>Ambush damage is now affected by weapon damage.  A harder hitting,\nheavier weapon will do more damage.  Keep in mind, however, that\nhigh bulk still reduces the damage done by ambush (and hence by\ntrap).  The advantages of harder hitting weapons are more apparent\nas the amount of hp of the victim goes down.</li>\n<li>Ambush and trap are less likely to succeed when the ambusher/trapper\nhas a high amount of skill encumbrance.</li>\n</ul>\n<p>Aesthetic changes:</p>\n<ul>\n<li>The 'time' command now shows the year and the age.</li>\n<li>Months from the calendar of the Steward's Reckoning will now be\ndisplayed with the full accents from Tolkien's work, if you have\nthe latin1 setting enabled.</li>\n</ul>\n<p>Miscellaneous changes:</p>\n<ul>\n<li>Some big changes to scripts (which were slated to go in previously,\nbut mysteriously did not make it) have gone in.  Be on the lookout\nfor scripts that may be malfunctioning, and notify an immortal if\nyou happen to find any such scripts.</li>\n</ul>\n<p>Enjoy,\nvakaur</p>","frontmatter":{"title":"News 1555: Trap and ambush changes","date":"October 09, 2017","author":null,"desc":null}}},"pathContext":{"slug":"/news/1555"}}

